'use client';

import React, { useState } from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import {
  MessageSquare,
  HeartHandshake,
  TrafficCone,
  Zap,
  Lock,
  Menu,
  X,
  ChevronDown,
  ChevronUp,
  Scale,
} from 'lucide-react';
import Image from 'next/image';

const navItems = [
  { href: '/chat', icon: MessageSquare, label: 'Chat with Cayla' },
];

const featureItems = [
  { href: '/features/relationship-advice', icon: HeartHandshake, label: 'Relationship Advice' },
  { href: '/features/traffic-light', icon: TrafficCone, label: 'Traffic Light System' },
  { href: '/features/tone-analyzer', icon: Zap, label: 'Tone Analyzer' },
  { href: '/mediator', icon: Scale, label: 'AI Mediator' },
];

const resourcesItems = [
  { href: '/privacy', icon: Lock, label: 'Privacy Policy' },
];

type NavItemProps = {
  href: string;
  icon: React.ElementType;
  label: string;
  pathname: string;
};

const NavItem: React.FC<NavItemProps> = ({ href, icon: Icon, label, pathname }) => {
  const isActive = pathname === href;
  return (
    <Link href={href} passHref>
      <div
        className={`flex items-center space-x-3 px-4 py-3 rounded-lg cursor-pointer transition-all duration-200 ease-in-out
          ${isActive
            ? 'bg-gradient-to-r from-blue-500 to-purple-600 text-white shadow-md'
            : 'text-gray-600 hover:bg-gray-100'
          }`}
      >
        <Icon className={`h-5 w-5 ${isActive ? 'text-white' : 'text-gray-400'}`} />
        <span className="font-medium">{label}</span>
      </div>
    </Link>
  );
};

export default function Sidebar() {
  const pathname = usePathname();
  const [featuresOpen, setFeaturesOpen] = useState(true);

  return (
    <aside className="w-64 flex-shrink-0 bg-white border-r border-gray-200 flex flex-col p-6">
      <div className="text-2xl font-bold bg-gradient-to-r from-blue-600 to-purple-600 bg-clip-text text-transparent mb-12">
        <Link href="/">Home</Link>
      </div>

      <nav className="flex-grow space-y-6">
        <div>
          <h3 className="px-4 mb-2 text-sm font-semibold text-gray-400 uppercase tracking-wider">Start Here</h3>
          {navItems.map((item) => (
            <NavItem key={item.href} {...item} pathname={pathname} />
          ))}
        </div>

        <div>
          <button
            className="w-full flex justify-between items-center px-4 mb-2 text-sm font-semibold text-gray-400 uppercase tracking-wider"
            onClick={() => setFeaturesOpen(!featuresOpen)}
          >
            <span>Features</span>
            {featuresOpen ? <ChevronUp size={16} /> : <ChevronDown size={16} />}
          </button>
          {featuresOpen && (
            <div className="space-y-1">
              {featureItems.map((item) => (
                <NavItem key={item.href} {...item} pathname={pathname} />
              ))}
            </div>
          )}
        </div>

      </nav>

      <div className="mt-auto">
        <h3 className="px-4 mb-2 text-sm font-semibold text-gray-400 uppercase tracking-wider">Resources</h3>
        {resourcesItems.map((item) => (
          <NavItem key={item.href} {...item} pathname={pathname} />
        ))}
      </div>
    </aside>
  );
} 